import React, { Suspense, useState, useMemo } from "react";
import { Box, Hidden, LinearProgress } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { renderRoutes } from "react-router-config";
import { useLocation } from "react-router-dom";
import {
  ConnectWalletButton,
  NavDrawer,
  TopBar,
  DrawerComp,
} from "app/components";
import { AppTheme } from "app/theme/types";
import TransactionDialog from "../TransactionDialog";
import WalletDialog from "../WalletDialog";
import { DevInfoBadge } from "./components";

const useStyles = makeStyles((theme: AppTheme) => ({
  root: {
    position: "relative",
    flex: 1,
    display: "flex",
    flexDirection: "column",
    background: theme.palette.background.gradient,
  },
  content: {
    position: "relative",
    display: "flex",
    flexDirection: "column",
    [theme.breakpoints.down("sm")]: {
      display: "block",
    },
    [theme.breakpoints.up("sm")]: {
      paddingLeft: theme.spacing(0),
    },
  },
  foot: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    margin: "20px auto",
    fontFamily: "ConthraxLt-Regular",
    letterSpacing: "1px",
    color: "#fff",
  },
}));

const MainLayout: React.FC<React.HTMLAttributes<HTMLDivElement>> = (
  props: any
) => {
  const { route } = props;
  const classes = useStyles();
  const [showDrawer, setShowDrawer] = useState(false);
  const [showTabDrawer, setShowTabDrawer] = useState(false);
  const location = useLocation();

  const currentPath: "swap" | "pool" | "bridge" | "zilo" | "ark" | "main" =
    useMemo(() => {
      const current = location.pathname;

      if (current.indexOf("/swap") === 0) return "swap";
      else if (
        current.indexOf("/pool") === 0 ||
        current.indexOf("/pools") === 0
      )
        return "pool";
      else if (
        current.indexOf("/bridge") === 0 ||
        current.indexOf("/history") === 0
      )
        return "bridge";
      else if (current.indexOf("/zilo") === 0) return "zilo";
      else if (current.indexOf("/arky") === 0) return "ark";
      else return "main";
    }, [location.pathname]);

  const onToggleDrawer = (override?: boolean) => {
    setShowDrawer(typeof override === "boolean" ? override : !showDrawer);
  };

  const onToggleTabDrawer = (override?: boolean) => {
    setShowTabDrawer(typeof override === "boolean" ? override : !showTabDrawer);
  };

  return (
    <Box className={classes.root}>
      <TopBar
        currentPath={currentPath}
        onToggleDrawer={onToggleDrawer}
        onToggleTabDrawer={onToggleTabDrawer}
      />
      <main className={classes.content}>
        <NavDrawer open={showDrawer} onClose={() => onToggleDrawer(false)} />
        <DevInfoBadge />
        <Suspense fallback={<LinearProgress />}>
          {renderRoutes(route.routes)}
        </Suspense>
      </main>
      <div className={classes.foot}>This is but a humble and grateful fork of <a href="https://github.com/Switcheo/zilswap-webapp" target="new">&nbsp;Zilswap</a>.</div>
      <Hidden smUp>
        <ConnectWalletButton />
      </Hidden>
      <WalletDialog />
      <TransactionDialog />
      <DrawerComp
        onClose={() => setShowTabDrawer(false)}
        open={showTabDrawer}
        navPath={currentPath}
      />
    </Box>
  );
};

export default MainLayout;
